.pop_up
    position: fixed
    display: flex
    justify-content: center
    align-items: center
    left: 0
    top: 0
    width: 100%
    height: 100%
    background-color: #0a0a0aa9
    z-index: 1

.pop_up_body
    padding: 1% 5% 5% 5%
    height: calc(200px + 30vw)
    width: calc(100px + 30vw)
    border-radius: calc(5px + 3vw)
    background: #5a5959ad

.pop_up_form
    display: flex
    flex-direction: column
    align-items: center
    justify-content: space-between
    height: 90%

.pop_up_input_label
    font-family: 'Varela Round', sans-serif
    font-style: normal
    font-weight: 400
    font-size:calc(12px + 2vw)
    color: #FFFFFF
    text-align: center
    text-decoration: none

.pop_up_input_text
    margin-bottom: 10%
    border-radius: 10px
    padding: 3%
    font-size: calc(5px + 2vw)
    height: calc(5px + 2vw)
    width: 100%

.pop_up_input_button
    height: calc(5px + 3vw)
    width: calc(5px + 10vw)
    border-radius: calc(5px + 3vw)
    font-size: calc(5px + 2vw)

.pop_up_x
    display: flex
    justify-content: center
    align-items: center
    background: #ffffff00
    border-radius: 100%
    font-size: calc(5px + 2vw)
    height: calc(10px + 2vw)
    width: calc(10px + 2vw)
