.UpPanel 
    display: flex
    max-height: 20px
    padding: 2% 5% 2% 4%
    justify-content: space-between
    align-items: center
    background-color:#363636

.Singup 
    background: #00000000
    border: none
    font-size: calc(8px + 0.7vw)
    color: aliceblue
    text-decoration: none
    width: calc(30px + 5vw)

.Singup:hover 
    color: #5f5f5f
    opacity: .2

.ButtonAutorization
    display: flex
    align-items: center

.Lupa_button
    display: flex
    align-items: center
    justify-content: center
    background-color: white
    color: black
    border: 3px solid #4718AB
    border-radius: calc(5px + 3vw)
    width: calc(20px + 3vw)
    height: calc(15px + 2vw)
    max-width: 50px
    max-height: 40px

.Log_in_botton 
    margin-left: 2%
    background-color: #ffffff
    border: 3px solid #4718AB
    border-radius: calc(5px + 3vw)
    font-size: calc(8px + 0.7vw)
    color: #000000
    max-width: 118px
    max-height: 40px
    width:  calc(40px + 2vw) 
    height: calc(10px + 4vw)
    text-align: center
    -webkit-transition-duration: 0.4s /* Safari */
    transition-duration: 0.4s
    text-decoration: none
    overflow: hidden
    cursor: pointer
    font-family: Quantico 

